@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&family=Lato:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  color: #111111;
}

body {
  background-color: #808080;
}

.container {
  max-width: 1440px;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.header {
  position: fixed;
  width: 100%;
  height: 112px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: #C16D6D;
  background: linear-gradient(45deg, #cc7777, #eb2f2f);
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
  border-radius: 0px;
  border-width: thin;
  z-index: 1;
}

.header-container {
  display: flex;
  height: 50%;
}

.name-header {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  padding-right: 5px;
  padding-left: 10px;
}

.maletag {
  margin: 25px 0px;
  padding-right: 20px;
}

.lvl-header {
  width: 115px;
  height: 22px;
  left: 12px;
  top: 13px;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
}

.small-text-left {
  position: absolute;
  width: 281px;
  height: 74px;
  left: 12px;
  top: 43px;
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 2px;
}

.small-text-main {
  position: absolute;
  width: 281px;
  height: 74px;
  left: 12px;
  top: 43px;
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: .8rem;
  line-height: 2px;
}

.small-text-right {
  position: absolute;
  width: 281px;
  height: 74px;
  left: 252px;
  top: 43px;
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 2px;
}

.small-text-container {
  display: flex;
  justify-content: space-between;
}

.header-container {
  width: 100vw;
  display: flex;
}

.main-container {
  position: absolute;
  background-color: #808080;
  height: calc(100vh - 170px);
  width: 50vw;
  top: 22%;
  left: 35.4%;
  z-index: 1;
  overflow-y: auto;
  flex: 1;
  border-top: none;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid black;
}

.main-top-bar {
  background: linear-gradient(45deg, #cc7777, #eb2f2f);
  position: fixed;
  top: 18%;
  height: 60px;
  width: 50vw;
  border: 1px solid;
  z-index: 2;
  display: flex;
  left: 35.4%;
  justify-content: space-between;
}

.main-name {
  position: absolute;
  width: 30%;
  height: 25px;
  left: 55px;
  top: -10px;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 28px;
}

.main-hp-text {
  position: absolute;
  width: 30%;
  height: 25px;
  left: 50%;
  top: -10px;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 28px;
}

.main-status-text {
  position: absolute;
  width: 65px;
  height: 25px;
  left: 75%;
  top: -10px;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 28px;
}

.pokemon-container {
  width: 50vw;
  margin-top: 0;
  background-color: #808080;
  flex: 1;
  height: 150vh;
  overflow-y: scroll;
}

.card-image {
  height: 115px;
  margin-top: 60px;
  margin-left: -4%;
  font-size: 0.78rem;
  font-family: 'Lato';
  font-style: normal;
  width: 50px;
  z-index: 1;
  align-items: center;
  justify-content: center;
  padding-right: 55px;
}

.card-name {
  height: 115px;
  margin-top: -115px;
  margin-left: 10%;
  font-size: 0.78rem;
  font-family: 'Lato';
  font-style: normal;
  width: 120px;
  z-index: 1;
}

.card-hp {
  margin-top: -119px;
  margin-left: 50%;
  font-family: 'Lato';
  font-style: normal;
  width: 90px;
  z-index: 2;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}

.card-status {
  height: 115px;
  margin-top: -70px;
  margin-left: 75%;
  font-size: 0.8rem;
  font-family: 'Lato';
  font-style: normal;
  width: 90px;
  z-index: 2;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  font-weight: bold;
  border-radius: 50px;
}

.burnStatus {
  border-radius: 5px;
  justify-content: center;
  align-self: center;
  font-size: 0.7rem;
}

.card {
  background-color: #808080;
}


.side-nav-container {
  position: fixed;
  width: 304px;
  height: 100vh;
  margin-top: 45%;
  padding-bottom: 200px;
}

.side-nav-list {
  height: auto;
  padding: 0;
  width: auto;
  padding-bottom: 200px;
  margin-top: -41%;
}

.side-nav-list .row {
  width: 100%;
  height: auto;
  background-color: #2D8FA5;
  border: 1px solid black;
  display: flex;
  padding: 10px;
  border-radius: 0 10px 10px 0;
  font-family: 'Roboto Condensed';
  list-style-type: none;
  padding-left: 10px;
}

.side-nav-list .row:hover {
  width: 100%;
  height: 50px;
  background-color: #2d8fa586;
  border: 1px solid black;
  display: flex;
  padding: 10px;
  border-radius: 0 10px 10px 0;
  font-family: 'Roboto Condensed';
  list-style-type: none;
}

.side-nav-list .row.admit-item {
  background-color: #6DC18F;
}

.side-nav-list .row.admit-item:hover {
  background-color: #589b73e5;
}

.side-nav-list .row.discharge-item {
  background-color: #B8B8B8;
}

.side-nav-list .row.discharge-item:hover {
  background-color: #797979;
}

.SideImageContainer {
  position: fixed;
  border: solid;
  height: 30%;
  width: 21%;
  background-color: aqua;
  margin-top: 11%;
  border-radius: 10px;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
}

.pokemon-sprite {
  width: 110%;
  height: 110%;
  padding-right: 40px;
  padding-bottom: 60px;
}

.top-nav-container {
  display: none;
}



